function _m(i,b,x) {
  const percentage = 100/i.length
  if(i[x] != undefined){ if(i[x] === b[x]){ return(percentage) }else{ return(0) }}else{return(0)}
  
}
function _match(ma,b) {
  return(
    _m(ma,b,0)+_m(ma,b,1)+_m(ma,b,2)+_m(ma,b,3)+_m(ma,b,4)+_m(ma,b,5)+_m(ma,b,6)+_m(ma,b,7)+_m(ma,b,8)+_m(ma,b,9)+_m(ma,b,10)
  )
}

